.profileImage {
    max-height: 20rem;
    border-radius: 15%;
    border-style: solid;
    transform: perspective(800px) rotateY(-20deg);
    box-shadow:  2em 4em 6em -2em rgba(0,0,0,.5),
    1em 2em 3.5em -2.5em rgba(0,0,0,.5);
    transition: 1.5s;
}

.profileImage:hover {
    transform: perspective(800px) rotateY(0deg) translateY(-1rem) rotateX(0deg);
    transition: 2s;
    filter: blur(0);
    opacity: 1;
}

.WelcomeMsgContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    float: left;
    /* border-style: solid;     */
    padding-top: 5rem;
    list-style: none;
    flex-wrap: wrap;
}

.WelcomeMsgTextContainer {
    /* border-style: solid;     */
    padding-right: 15rem;
}

.WelcomeMsg1Container {
    display: flex;
    /* border-style: solid;     */
    font-size: 2rem;
    gap: 1rem;
}

.WelcomeMsg2Container {
    display: flex;
    padding-top: 1.5rem;
    font-size: 1.7rem;
    align-items: end;
}

.WelcomeMsg2_1 {
    font-size: 2rem;
    font-family: Helvetica;
    font-weight: bolder;
    color: darkblue;
    padding-right: 0.8rem;
}

.WelcomeMsg3Container {
    display: flex;
    /* flex-direction: row; */
    /* justify-content: space-between; */
    align-items: baseline;
    /* border-style: solid; */
    font-size: 2.5rem;
    min-width: 25rem;
    padding-top: 5rem;
}

.WelcomeMsg3_1 {
    padding-right: 0.8rem;
}

/* .RTE1 {
    border-style: solid;
} */

.waving {
    animation: wave 2.5s infinite;
    transform-origin: 70% 70%;
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(14deg);
    }
    20% {
        transform: rotate(-8deg);
    }
    30% {
        transform: rotate(14deg);
    }
    40% {
        transform: rotate(-4deg);
    }
    50% {
        transform: rotate(10deg);
    }
    60% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@media (max-width: 1000px) {
    .WelcomeMsgContainer {
        flex-direction: column;

    }

    .WelcomeMsgTextContainer {
        padding-right: 0rem;
        padding-bottom: 5rem;
    }

    .WelcomeMsg3Container {
        padding-top: 3rem;
    }
}